<template>
  <div class="single-blog">
        <h2 v-changeColor>标题:{{blog.title}}</h2>
        <article>内容详情:{{blog.content}}</article>
        <div v-for="typ in blog.types" :key="typ">
            分类:{{typ}}
        </div>
        <p>作者:{{blog.author}}</p>
        <button @click.prevent="del">删除</button>
        <router-link :to="'/edit/' + id">编辑博客</router-link>
  </div>
</template>

<script>
export default {
    data (){
        return {
            blog:[],
            id:this.$route.params.id,
        }
    },
    created (){
        this.$http.get("https://wd2515197169kuzyet.wilddogio.com/poststyq/" + this.id + ".json")
        .then(function(data){
            return data.json();
        }).then(function (data){
            this.blog = data;
            console.log(data);
        })
    },
    methods:{
        del (){
            this.$http.delete("https://wd2515197169kuzyet.wilddogio.com/poststyq/" + this.id + ".json")
            .then(function (respones){
                this.$router.push({path:'/ShowBlog'})
            })
        }
    }
}    
</script>

<style scoped>
.single-blog{
    max-width: 960px;
    margin: 10px auto;
    padding: 20px 0;
    background: #eee;
    border: 1px solid #aaa;
}
button {
    max-width: 100px;
    background: rgb(105, 212, 105);
    cursor: pointer;
}
</style>



