<template>
    <div v-themn="'wide'" id="show-blog">
        <h1>博客总览</h1>
        <input type="text" placeholder="搜索" v-model="searchs">
        <div class="single-blog" v-for="blog in filterBolgs" :key="blog.title">
            <router-link :to="'/ShowBlog/'+ blog.id"><h2 v-changeColor>{{blog.title | uppercase}}</h2></router-link>
            <article>{{blog.content | snippet}}</article>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'show-blog',
        data (){
            return {
                blogs:[],
                searchs:""
            }
        },
        //dom创建之前
        created (){
            this.$http.get("https://wd2515197169kuzyet.wilddogio.com/poststyq.json")
            .then(function (data){
                // console.log(data);
                // this.blogs = data.body.slice(0,10);
                return data.json();
            }).then(function(data){
                var blogsArray = [];
                for(let key in data){
                    data[key].id = key;
                    blogsArray.push(data[key]);
                }
                this.blogs = blogsArray;
            })
        },
        computed:{
            filterBolgs:function (){
                return this.blogs.filter((blog) => {
                    return blog.title.match(this.searchs);
                })
            }
        },
        //局部过滤器
        filters:{
            uppercase:function (value){
                return value.toUpperCase();
            }
        }
    }
</script>


<style scoped>
#show-blog{
    max-width: 800px;
    margin: 0 auto;
}
.single-blog{
    padding: 20px;
    margin: 20px 0;
    box-sizing: border-box;
    background: #eee;
}
input[type="text"]{
    max-width: 100%;
    padding: 8px 0;
    border-radius: 4px;
    border: 1px solid rgb(130, 194, 247);
}
#show-blog a{
    color: #444;
    text-decoration: none;
}
</style>



