<template>
  <div id="add-blog">
      <!-- 添加博客内容 -->
    <h2>编辑博客</h2>
    <form v-if="!submited">
        <div>
            <label>博客标题</label>
            <input type="text" v-model="blog.title">
        </div>
        <div>
            <label>博客内容</label>
            <textarea cols="30" rows="10" v-model="blog.content"></textarea>
        </div>

        <div id="checkboxes">
            <label>Vue.js</label>
            <input type="checkbox" value="Vue.js" v-model="blog.types">
            <label>Node.js</label>
            <input type="checkbox" value="Node.js" v-model="blog.types">
            <label>React.js</label>
            <input type="checkbox" value="React.js" v-model="blog.types">
            <label>Angular4</label>
            <input type="checkbox" value="Angular4" v-model="blog.types">
        </div>
        <!-- 作者 -->
        <label>作者:</label>
        <input type="text" v-model="blog.author">
        <!-- 添加博客 -->
        <button @click.prevent="post">编辑博客</button>
    </form>
    <!-- 显示添加博客后的内容  -->
    <div v-if="submited">
        <h3>您的博客编辑成功！</h3>
    </div>
    <!-- 展示博客内容 -->
    <div id="preview">
        <h3>博客总览</h3>
        <p>博客标题:{{blog.title}}</p>
        <p>博客内容：</p>
        <p>{{blog.content}}</p>
        <p>博客分类:</p>
        <ul>
            <li v-for="types in blog.types" :key="types">{{types}}</li>
        </ul>
        <p>作者:{{blog.author}}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'add-blog',
  data () {
    return {
        //是否展示
        submited:false,
        //博客数据
      blog:{}, 
      id:this.$route.params.id,
    }
  },
  methods:{
    //   点击添加博客执行的方法
      post (data) {
          this.$http.put("https://wd2515197169kuzyet.wilddogio.com/poststyq/" + this.id + ".json",this.blog)
          .then(function(data){
              this.submited = true;
          });
      }
  },
  created(){
      this.$http.get("https://wd2515197169kuzyet.wilddogio.com/poststyq/" + this.id + ".json")
      .then((response)=>{
          this.blog = response.body;
      })
      
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#add-bolg *{
    box-sizing: border-box;
}

#add-blog {
    margin: 20px auto;
    max-width: 600px;
    padding: 20px;
}
label {
    display: block;
    margin: 20px 0 10px;
}
input[type="text"],textarea,select {
    display: block;
    width: 100%;
    padding: 8px;
}
textarea{
    height: 200px;
}

#checkboxes label {
 display: inline-block;
 margin-top: 10px;
}
#checkboxes input {
 display: inline-block;
 margin-left: 10px;
}

button{
   display: block;
   margin: 20px 0;
   background: crimson;
   color: #fff;
   border: 0;
   padding: 14px;
    border-radius: 4px;
    font-size: 18px;
    cursor: pointer;
}

#preview {
    padding: 10px 20px;
    border: 1px dotted #ccc;
    margin: 30px 0;
}
h3{
    margin-top: 10px;
}

</style>




